@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;700&display=swap');

html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  height: 100%;
}

.App {
  text-align: center;
  width: 1600px;
  height: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow: hidden;
  background-color: #FFFFFF;
}

.App-header {
  position: relative;
  top: -4px;
  background-color: #FFFFFF;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-header-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #344b8e;
  color: white;
  border-bottom: 1px solid #344b8e;
}

.App-nav-top {
  position: relative;
  top: -13px;
  left: 0;
  width: 100%;
  display: flex;
  align-items: flex-start;
  background-color: #344b8e;
  color: white;
}
.App-nav-bottom {
  position: relative;
  top: 709px;
  left: 0;
  width: 100%;
  display: flex;
  align-items: flex-start;
  background-color: #344b8e;
  color: white;
}


.App-title {
  font-size: 3.5rem;
  margin: 0;
  padding-left: 1rem;
  color: #ffa51f;
  text-shadow: 2px 2px 4px #344b8e;
  font-family: 'Anton', sans-serif;
}

.App-nav {
  display: flex;
  margin-top: -550px;
  margin-left: -70px;
  justify-content: flex-start;
}

.App-link {
  display: inline-block;
  padding: 1rem 6.75rem;
  font-size: 1.4rem;
  color: white;
  background-color: #ffa51f;
  border: none;
  border-radius: 1px;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  /*margin: 0 .5rem;*/
  transition: background-color 0.3s, box-shadow 0.3s;
  font-family: 'Public Sans', sans-serif;
}

.App-link:hover {
  background-color: #ffd89e;
  color: #282c34;
  text-decoration: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

